export const elapsedTime = (date: number) => {
  if (date === 0) {
    return "  ";
  }
  const start = new Date(date * 1000);
  const end = new Date();

  const diff = (+end - +start) / 1000;

  const times = [
    { name: "", milliSeconds: 60 * 60 * 24 * 365 },
    { name: "", milliSeconds: 60 * 60 * 24 * 30 },
    { name: "", milliSeconds: 60 * 60 * 24 },
    { name: "", milliSeconds: 60 * 60 },
    { name: "", milliSeconds: 60 },
  ];

  for (const value of times) {
    const betweenTime = Math.floor(diff / value.milliSeconds);

    if (betweenTime > 0) {
      return `${betweenTime}${value.name} `;
    }
  }
  return " ";
};
